.video {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.video .videoWrapper {
  flex: 5;
  padding: 8px;
}

.video .videoWrapper .videoFrame {
  width: 100%;
  max-height: 500px;
  background-color: red;
  object-fit: cover;
}

.video .videoWrapper .details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.video .videoWrapper .details .btngrp {
  display: flex;
  gap: 25px;
  align-items: center;
  justify-content: center;
}

.video .videoWrapper .details .btngrp .btn {
  display: flex;
  gap: 5px;
  align-items: center;
  cursor: pointer;
  justify-content: center;
}

.video .videoWrapper .details .info {
  color: rgba(255, 255, 255, 0.676);
}
.video .videoWrapper .title {
  margin-top: 15px;
  margin-bottom: 15px;
}
.video .recommendedWrapper {
  flex: 2;
  padding: 8px;
}

.video .videoWrapper .channel {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-top: 45px;
  gap: 15px;
}
.video .videoWrapper .channel .details {
  display: flex;
  align-items: start;
  justify-content: center;
  gap: 15px;
}
.video .videoWrapper .channel img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.video .videoWrapper .info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.video .videoWrapper .channelTitle {
  color: white;
  font-weight: 500;
}
.video .videoWrapper .channelCount {
  font-weight: 300;
}
.video .videoWrapper .channelDescription {
  border-radius: 15px;
  line-height: 25px;

  margin-top: 18px;
  background-color: #5f5d5d55;
  padding: 15px;
}
